  fill: #c6c6c6;
  width: 1em;
  height: 1em; }
  .icon-rotate {
    animation: rotate 1s infinite; }

.button {
  background-color: #f1f1f1;
  border: 1px solid #bbb;
  border-radius: 0.2rem;
  font-weight: 600;
  padding: 0.75em;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.25s, box-shadow 0.25s; }
  .button:disabled {
    cursor: not-allowed; }
  .button:hover {
    background-color: #f4f4f4;
    box-shadow: 0 0 3px 0 #bbb; }

.mark {
  background-color: #bbb;
  color: #fbfbfb;
  padding: 0.5em 1em;
  border-radius: 0.2rem; }

.item-container {
  width: 100%; }

.item-header {
