      align-items: center;
      padding: 1em 0;
      border: none; } }
  .item-field {
    padding: 0.75em 0.5em; }
    .item-field::before {
      content: attr(data-field);
      font-style: italic; }
    @media screen and (min-width: 524px) {
      .item-field {
        padding: 0 0.5em;
        flex-basis: calc(100% / 4);
        flex-grow: 1; }
        .item-field::before {
          display: none; } }
    @media screen and (min-width: 710px) {
      .item-field {
        flex-basis: calc(50% / 3); } }
  .item-title {
