.notification {
  display: flex;
  background-color: #fff;
  align-items: stretch;
  line-height: 1.8;
  border-radius: 0.2rem;
  margin-bottom: 1em;
  height: 2.75rem;
  max-height: 2.75rem;
  box-shadow: 0 0 0 1px transparent inset;
  transform: translateX(calc(100% - 2.75rem - 2px));
  transition: transform 0.3s ease-out, box-shadow 0s 0.3s, margin-right 0.5s 5s, opacity 0.4s 5.1s, max-height 0.3s 5.8s, margin-bottom 0.3s 5.8s; }
  .notification:hover {
    transform: translateX(0);
    box-shadow: 0 0 0 1px #bbb inset;
    transition: transform 0.3s ease-in, box-shadow 0s 0s, margin-right 0.5s 5s, opacity 0.4s 5.1s, max-height 0.3s 5.8s, margin-bottom 0.3s 5.8s; }
  .notification:hover .notification-message {
    visibility: visible;
    transition: visibility 0s 0s; }
  .notification:hover .notification-icon {
    border-radius: 0.2rem 0 0 0.2rem;
    transition: border-radius 0.2s 0.3s; }
  @media screen and (min-width: 1281px) {
